@import "ten.scss";
@import "car.scss";
@import "nav.scss";
@import "_cards/base.scss";

html {
  scroll-behavior: smooth;
}

html,
body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-family: 'Raleway', sans-serif;
}

img {
  max-width: 100%;
}

h1,
h2 {
  margin: 0;
}

h1 {
  font-size: 64px;
  margin-bottom: 16px;
}

h2 {
  font-size: 48px;
  margin-bottom: 16px;
}

.main-text {
  font-size: 22px;
  line-height: 1.6;
  margin-bottom: 40px;
  margin-top: 0;
}

.text-bold {
  font-weight: bold;
}

.promo_text {
  margin: 20px auto;
  max-width: 800px;
  font-size: 20px;
  line-height: 1.6;
  text-align: center;
}

.promo {
  padding-bottom: 160px;
  color: white;
  background: linear-gradient(243.96deg, #05ccbb 10.6%, rgba(79, 154, 247, 0.961912) 49.07%, rgba(167, 89, 255, 0.926133) 85.89%);

  h1 {
    margin-top: 50px;
    font-size: 74px;
    text-align: center;
  }

  .container {
    text-align: center;
  }

  .btn {
    margin-top: 20px;
  }
}

.container {
  margin: 0 auto;
  padding: 0 16px;
  max-width: 1190px;
  overflow: hidden;
}

.row {
  display: flex;
}

.bg-teal {
  background-color: #07c29e;
}

.bg-violet {
  background-color: #a353ff;
}

.bg-orange {
  background-color: #f59d20;
}

.history {
  position: relative;
  top: -100px;
  margin-bottom: -140px;
  padding: 150px 0;
  text-align: center;
  color: white;
  background-image: url("../img/history-bg.svg");
  background-repeat: no-repeat;
  background-size: cover;
}

a.btn {
  display: inline-block;
  padding: 10px 20px;
  font-size: 24px;
  text-decoration: none;
  color: white;
  border: 2px solid white;
  border-radius: 8px;
  background-color: #a25bfc;
  box-shadow: 3px 3px 0 0 white, 5px 5px 0 0 white, 7px 7px 0 0 #139b80;
  transition: all 100ms linear;
  &:hover {
    transform: translateX(5px) translateY(5px);
    box-shadow: 1px 1px 0 0 white, 2px 2px 0 0 white, 3px 3px 0 0 #066754;
    background-color: #9247f1;
  }
}

.alumni {
  position: relative;
}

.facts {
  position: relative;
  top: -10px;
  margin-bottom: -140px;
  padding: 100px 0;
  text-align: center;
  color: white;
  background-image: url('../img/facts-bg.svg');
  background-repeat: no-repeat;
  background-size: cover;

}

.preload {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #191d21;
  z-index: 1000;

  .container {
    display: flex;
    height: 100vh;
    justify-content: center;
    align-items: center;
  }

  .ten {
    .brick {
      animation: preload 2s 1s linear;

      &.duration-1 {
        animation-duration: 1s;
        animation-delay: 4s;
      }

      &.duration-2 {
        animation-duration: 2s;
        animation-delay: 3s;
      }

      &.duration-3 {
        animation-duration: 3s;
        animation-delay: 5s;
      }

      &.duration-4 {
        animation-duration: 4s;
        animation-delay: 10s;
      }

      &.duration-5 {
        animation-duration: 2s;
        animation-delay: 15s;
      }
    }
  }
}

.color-teal {
  color: #07c29e;
}

.practice {
  .card__title {
    text-align: left;
    font-size: 20px;
  }
  .card {
    font-size: 16px;
    box-shadow: 0 0 12px 0 rgba(black, 0.2);
  }
}


@media (max-width: 800px) {
  .promo h1 {
    font-size: 48px;
  }
  h1 {
    font-size: 40px;
  }
  h2 {
    font-size: 30px;
  }
  .main-text {
    font-size: 18px;
  }
}
