.main-nav {
  padding-top: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  a {
    color: #fff;
    text-decoration: none;
  }
}

.main-nav_menu {
  display: flex;
  list-style: none;

  .menu_item {
    padding: 5px 10px;
  }
}

@media (max-width: 800px) {
  .main-nav_menu {
    display: none;
  }
}

.main-nav.main-nav--inner {
  a {
    color: #1b1e21;
  }

  margin-bottom: 80px;
}

.main-nav_logotype {
  flex-shrink: 0;
}
