.car-animation {
  position: relative;
  width: 100%;
  height: 400px;
}

@media (max-width: 800px) {
  .car-animation {
    display: none;
  }
}


.car-bg {
  position: absolute;
  left: 0;
}

.car-bg-1 {
  animation: speed 1800ms linear infinite;
}

.car-bg-2 {
  animation: speed 1800ms 600ms linear infinite;
}

.car-bg-3 {
  animation: speed 1800ms 1200ms linear infinite;
}

.car-fire {
  position: absolute;
  right: 0;
  bottom: 0;
  z-index: 250;
  opacity: 0;
  animation: fire 5s linear 5s infinite;
}

.car {
  position: absolute;
  width: 1200px;
  height: 400px;
  overflow: visible;
  animation: car-rotate 5s linear 5s infinite;
  z-index: 200;


  .round-1 {
    position: absolute;
    bottom: 0;
    left: 115px;
    z-index: 100;
    transform: rotate(0deg);
  }
  .round-2 {
    position: absolute;
    bottom: 0;
    right: 180px;
    z-index: 100;
    transform: rotate(90deg);
  }

  .car-body {
    position: absolute;
    left: 0;
    bottom: 60px;
    z-index: 100;
    animation: car-body-rotate 5s linear 5s infinite;
  }
}

.round-1 {
  animation: round-1 2s linear infinite;
}

.round-2 {
  animation: round-2 2s linear infinite;
}

@keyframes round-1 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes round-2 {
  0% {
    transform: rotate(60deg);
  }
  100% {
    transform: rotate(420deg);
  }
}

@keyframes car-rotate {
  0% {
    transform: rotate(0);
    transform-origin: right bottom;
  }
  20% {
    transform: rotate(2deg);
  }
  30% {
    transform: rotate(0deg);
  }
}

@keyframes car-body-rotate {
  0% {
    transform: rotate(0);
    transform-origin: right bottom;
  }
  20% {
    transform: rotate(1deg);
  }
  30% {
    transform: rotate(-0.35deg);
  }
  35% {
    transform: rotate(0deg);
  }
  40% {
    transform: rotate(0deg);
  }
}

@keyframes speed {
  0% {
    transform: translateX(-450px);
  }
  100% {
    transform: translateX(900px);
  }
}

@keyframes fire {
  0% {
    transform: translateX(-220px) translateY(-10px) scale(0.9);
    opacity: 0;
    width: 20px;
  }
  20% {
    transform: translateX(-100px) translateY(-10px) scale(1);
    opacity: 1;
    width: 180px;
  }
  40% {
    transform: translateX(100px) translateY(-10px) scale(0.9);
    opacity: 0;
  }
  100% {
    transform: translateX(-100px) translateY(-10px) scale(0.9);
    opacity: 0;
  }
}
