.card-deck {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 0 24px;
}

@media (max-width: 800px) {
  .card-deck {
    flex-direction: column;
    align-items: center;
  }
}


.card {
  position: relative;
  width: 100%;
  max-width: 400px;
  min-width: 0;
  word-wrap: break-word;
  background-color: white;
  background-clip: border-box;
  color: #191d21;
  font-size: 20px;
  overflow: hidden;
  padding: 30px;
  margin: 20px;
  border-radius: 16px;
  box-shadow: 0 0 10px 0 rgba(#5d3091, 0.6);
  transition: all 200ms linear;
  text-decoration: none;
  cursor: pointer;

  &:hover {
    transform: scale(1.025) translateY(-2px);
    box-shadow: 0 0 20px 0 rgba(#5d3091, 0.3);
  }

  .card__title {
    display: block;
    width: 100%;
    text-align: center;
    font-size: 24px;
    margin: 10px 0;
  }

  .card__img {
    width: 100%;
    text-align: center;
  }
}
